import React from 'react'
import "./Footer.css"
import {FiArrowRight} from "react-icons/fi"
import {BsInstagram} from "react-icons/bs";
import {FaGithub} from "react-icons/fa";
import {AiFillFacebook} from "react-icons/ai"
import {MdLocationPin} from "react-icons/md"
import {AiOutlineClockCircle} from "react-icons/ai"
import {BsTelephoneFill} from "react-icons/bs"
import Typical from "react-typical"
import {Link} from "react-router-dom"
import IMG from "../../images/avatar-01.png"
import IMG1 from "../../images/avatar-02.png"
import IMG2 from "../../images/avatar-03.png"
import IMG3 from "../../images/avatar-04.png"
import IMG4 from "../../images/avatar-05.png"
import IMG5 from "../../images/avatar-06.png"


const Footer = () => {
  return (

<div className="footer">
<div className="container">
  <div className="box">
  <Link to="/">
  <h3  className="logo">
  <Typical 
    loop={Infinity}
    wrapper="b"
    steps={[
      "Appondas",
      1000,
      "A",
      900,
      "Ap",
      800,
      "App",
      700,
      "Appo",
      600,
      "Appon",
      500,
      "Appond",
      400,
      "Apponda",
      300,
      "Appondas",
      200,            
    ]}  />
  
  </h3>
  </Link>


    <ul className="social">
      <li>
        <a href="#"  className="facebook icon" traget="_blank"><AiFillFacebook/></a>

      </li>
      <li>
      <a href="#" className='github icon'  traget="_blank"><FaGithub/></a>
      </li>
      <li>
      <a href="#"  className="instagram icon" traget="_blank"><BsInstagram/></a>
      </li>
    </ul>
    <p className="text">
      Lorem ipsum, dolor sit amet consectetur adipisicing elit. Temporibus nulla rem, dignissimos iste aspernatur
    </p>
  </div>
  <div className="box">
    <ul className="links">
      <li><FiArrowRight className="icon" /><a href="#">Home</a></li>
      <li><FiArrowRight className="icon" /><a href="#services">Services</a></li>
      <li><FiArrowRight className="icon" /><a href="#industries">Industries</a></li>
      <li><FiArrowRight className="icon" /><a href="#about">About</a></li>
      <li><FiArrowRight className="icon" /><a href="#work">Work</a></li>
    </ul>
  </div>
  <div className="box">
    <div className="line">
      <MdLocationPin className="icon" />
      <div className="info">Egypt, Giza, Inside The Sphinx, Room Number 220</div>
    </div>
    <div className="line">
      <AiOutlineClockCircle className="icon" />
      <div className="info">Business Hours: From 10:00 To 18:00</div>
    </div>
    <div className="line">
      <BsTelephoneFill className="icon" />
      <div className="info">
        <span>+20123456789</span>
        <span>+20198765432</span>
      </div>
    </div>
  </div>
  <div className="box footer-gallery">
    <img src={IMG}  alt="iso" />
    <img src={IMG1} alt="iso" />
    <img src={IMG2} alt="iso" />
    <img src={IMG3} alt="iso" />
    <img src={IMG4} alt="iso" />
    <img src={IMG5} alt="iso" />
    
    
  </div>
</div>
<p className="copyright"> &copy; Appondas 2022</p>

</div>
  )
}

export default Footer